body {
  display: flexbox;
  background-image: url("https://cdn.pixabay.com/photo/2018/05/01/07/52/tuscany-3364921_1280.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Times New Roman, Times, serif;
}

.clock {
  width: 560px;
  height: 560px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("https://i.ibb.co/dBGxSK1/hiclipart-com-3.png");
  background-size: cover;
  border-radius: 50%;
  margin-left: 12px;
  position: relative;
  top: auto;
  left: 350px;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translateX(-50%);
}

.clock-center {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 268px;
  left: 268px;
}

.clock-seconds_arrow {
  width: 5px;
  height: 40%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes_arrow {
  width: 7px;
  height: 33%;
  bottom: 50%;
  left: 50%;
}

.clock-hours_arrow {
  width: 12px;
  height: 24%;
  bottom: 50%;
  left: 50%;
}

.date {
  width: 100px;
  height: 100px;
  color: #000;
}

.calendar {
  justify-content: space-between;
  display: flex;
}

.date-style {
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 40px;
  font-weight: 900;
  display: flex;
}

.digital-clock {
  text-align: center;
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 60px;
}

